.hero {
  background-color: #021526; 
  position: relative;
  top: 60px;
  height: 700px;
  z-index: 1; 
}

.card {
  position: relative;
  left: 90px;
  top: -10px;
  width: 450px;
  height: 254px;
  background-color: #000;
  display: flex;
  flex-direction: column;
  justify-content: top;
  padding: 12px;
  gap: 12px;
  border-radius: 8px;
  cursor: pointer;
}

.card p {
  position: relative;
  font-weight: bold;
  background: linear-gradient(45deg, #e81cff, #40c9ff); 
      -webkit-background-clip: text; 
      -webkit-text-fill-color: transparent; 
      background-clip: text; 
  color: transparent; 
  position: relative;
  top: -50px;
  font-size: 40px;
  color: white;
  top: 30px;
}

.card::before {
  content: '';
  position: absolute;
  inset: 0;
  left: 5px;
  margin: auto;
  width:485px;
  height: 288px;
  border-radius: 10px;
  background: linear-gradient(-45deg, #e81cff 0%, #40c9ff 100% );
  z-index: -10;
  pointer-events: none;
  transition: all 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.card::after {
  content: "";
  z-index: -1;
  position: absolute;
  inset: 0;
  background: linear-gradient(-45deg, #fc00ff 0%, #00dbde 100% );
  transform: translate3d(0, 0, 0) scale(0.95);
  filter: blur(20px);
  height: 284px;
}

.heading {
  font-size: 50px;
  text-transform: capitalize;
  font-weight: 700;
}

.card p:not(.heading) {
  font-size: 14px;
}

.card p:last-child {
  color: #e81cff;
  font-weight: 600;
}

.card:hover::after {
  filter: blur(30px);
}

.card:hover::before {
  transform: rotate(-90deg) scaleX(1.34) scaleY(0.77);
}


.description {
  font-size: 40px;
  text-transform: capitalize;
  font-weight: 700;
  text-align: left;
}

